.container {
  position: relative;
  height: 100vh;
  width: 688px;
  margin: 0 auto;
  overflow: hidden;
  white-space: nowrap;

  @media screen and (max-width: 1024px) {
    width: 410px;
  }

  @media screen and (max-width: 768px) {
    display: none;
  }
}

.slider {
  width: 688px;
}

.bullet {
  width: 8px;
  height: 100%;
  background-color: #eee;
  display: inline-block;
  border-radius: 50%;
  cursor: pointer;
}

.activeBullet {
  background-color: #0ec261;
  opacity: 1;
}

.bulletsContainer {
  width: 56px;
  height: 8px;
  position: absolute;
  bottom: 32px;
  display: flex;
  justify-content: space-between;
  margin: 0 calc(50% - (56px / 2));
}

.nextButton {
  height: 100%;
  width: 64px;
  color: #eee;

  &:hover {
    background-color: #0ec261;
    opacity: 0.2;
  }

  ::after {
    content: 'next';
  }
}
